@font-face {
    font-family: 'fontello';
    src: url('font/fontello.eot?54814241');
    src: url('font/fontello.eot?54814241#iefix') format('embedded-opentype'),
    url('font/fontello.woff2?54814241') format('woff2'),
    url('font/fontello.woff?54814241') format('woff'),
    url('font/fontello.ttf?54814241') format('truetype'),
    url('font/fontello.svg?54814241#fontello') format('svg');
    font-weight: normal;
    font-style: normal;
}

[class^="icon-"]:before,
[class*=" icon-"]:before {
    font-family: "fontello";
    font-style: normal;
    font-weight: normal;
    speak: none;

    display: inline-block;
    text-decoration: inherit;
    width: 1em;
    margin-right: .2em;
    text-align: center;
    /* opacity: .8; */

    /* For safety - reset parent styles, that can break glyph codes*/
    font-variant: normal;
    text-transform: none;

    /* fix buttons height, for twitter bootstrap */
    line-height: 1em;

    /* Animation center compensation - margins should be symmetric */
    /* remove if not needed */
    margin-left: .2em;

    /* you can be more comfortable with increased icons size */
    /* font-size: 120%; */

    /* Font smoothing. That was taken from TWBS */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;

    /* Uncomment for 3D effect */
    /* text-shadow: 1px 1px 1px rgba(127, 127, 127, 0.3); */
}


.icon {
    &-twitter:before {
        content: '\f099';
    }

    &-github:before {
        content: '\f09b';
    }

    &-mac:before {
        content: '\f179';
    }

    &-windows:before {
        content: '\f17a';
    }

    &-linux:before {
        content: '\f17c';
    }

    &-safari:before {
        content: '\f267';
    }

    &-chrome:before {
        content: '\f268';
    }

    &-firefox:before {
        content: '\f269';
    }

    &-opera:before {
        content: '\f26a';
    }

    &-internet-explorer:before {
        content: '\f26b';
    }

    &-microsoft-edge:before {
        content: '\f282';
    }
}


